<template>
    <div v-show="!moment">
        <label class="r-36-gray" @click="updateShow" v-html="t('audioteca.title')"></label>
        <br><br>
        <div>
            <p class="m-16-gray px-4 pt-2 pb-1" v-html="t('audioteca.info')">

            </p>
        </div>

        <div class="audio-player">
            <canvas ref="canvasRef" width="256" height="70" class="visualizer"></canvas>

            <div class="song-info">
                <h3>{{ currentSongName }}</h3>
                <div class="time">
                    {{ formatTime(currentTime) }} / {{ formatTime(duration) }}
                </div>
                <input type="range" min="0" :max="duration" v-model="currentTime" @input="seekAudio" />
            </div>
            <div class="d-flex pb-5 pt-2">
                <img src="../assets/Atras.png" class="m-auto" @click="prevTrack">
                <img :src="isPlaying ? pauseIcon : playIcon" style="height: 100%; width: fit-content; margin: auto;"
                    @click="togglePlay">
                <img src="../assets/Siguiente.png" class="m-auto" @click="nextTrack">
                <button @click="toggleShuffle" :class="{ active: isShuffle }" class="shuffle-button m-auto">
                    <img v-show="!isShuffle" src="../assets/Aleatorio-dis.png" class="m-auto" width="35">
                    <img v-show="isShuffle" src="../assets/Aleatorio-ava.png" class="m-auto" width="35">
                </button>
            </div>
        </div>
        <div class="fullblack py-1">
            <p class="sb-20-white text-start" v-html="t('audioteca.audioejercicios')">

            </p>
            <div style="position: absolute; right: 20px; margin-top: -20px;">
                <img src="../assets/heart.png" class="m-auto">
            </div>
        </div>
        <br>
    </div>

    <div v-show="moment">
        <div>
            <div>
                <label class="sb-20-black pb-4" v-html="t('when.estabas')"></label>
                <ul class="ps-0">
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-m" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-m" class="form-check-label" v-html="t('when.act1')"></label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-s" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-s" class="form-check-label" v-html="t('when.act2')">
                            </label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-a" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-a" class="form-check-label" v-html="t('when.act3')"></label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-4" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-4" class="form-check-label" v-html="t('when.act4')"></label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-5" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-5" class="form-check-label" v-html="t('when.act5')"></label>
                        </div>
                    </li>

                </ul>
                <div class="d-flex justify-content-center pt-3 pb-5">
                    <div class="px-3">
                        <button type="button" class="btn-gray-white" @click="SetMoment()" v-html="t('homeplay.omitir')">

                        </button>
                    </div>
                    <div class="px-3">
                        <button type="button" class="btn-black border-0" @click="SetMoment()"
                            v-html="t('homeplay.guardar')">

                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <br><br>


    <div class="lista" ref="listRef">
        <transition-group name="list" tag="div">
            <div v-for="(song, index) in songs" :key="song.id" class="lisongs b-16-gray btn-group"
                :class="{ 'is-source': isDragging && index === currentIndex }" role="group"
                @touchstart="startTouch(index, $event)" @touchmove.prevent="onDrag($event)" @touchend="endDrag"
                @touchcancel="endDrag">
                <div>
                    <canvas v-show="index === currentIndex && isPlaying" :ref="el => setMiniCanvasRef(el, index)"
                        width="40" height="25" class="mini-visualizer px-1">
                    </canvas>

                    <button style="width: 48px;" v-show="index != currentIndex" class="btn-audio"
                        @click.stop="playSong(index)">
                        <img src="../assets/Play.png" width="12" class="m-auto">
                    </button>
                </div>

                <span style="flex: 1; cursor: pointer;" class="text-start span-audio">
                    <label disabled v-if="editingIndex !== index">{{ song.name }}</label>
                    <input v-else v-model="editedName" @keyup.enter="saveEdit(index)" @blur="saveEdit(index)" />
                </span>

                <button class="btn-audio" @click.stop="editSong(index)">
                    <img src="../assets/Edit.png" class="m-auto">
                </button>
                <button class="btn-audio" @click.stop="removeSong(index)">
                    <img src="../assets/Delete-song.png" class="m-auto">
                </button>
            </div>
        </transition-group>
    </div>

    <div v-if="isDragging" class="floating" :style="floatingStyle">
        {{ dragText }}
    </div>
    <br><br><br>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, computed } from 'vue'
import { useI18n } from 'vue-i18n'

const { t } = useI18n()


const songs = ref([
    { id: 1, name: 'AP 1', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_2.mp3', import.meta.url).href },
    { id: 2, name: 'AP 2', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_6.mp3', import.meta.url).href },
    { id: 3, name: 'AP 3', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_7.mp3', import.meta.url).href },
    { id: 4, name: 'AP 4', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_8.mp3', import.meta.url).href },
    { id: 5, name: 'AP 5', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_9.mp3', import.meta.url).href },
    { id: 6, name: 'AP 6', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_11.mp3', import.meta.url).href },
    { id: 7, name: 'AP 7', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_21.mp3', import.meta.url).href }
])

const playIcon = new URL('@/assets/Play.png', import.meta.url).href
const pauseIcon = new URL('@/assets/Pausa.png', import.meta.url).href

const currentIndex = ref(0)
const moment = ref(false)

const isPlaying = ref(false)
const isShuffle = ref(true)
const currentTime = ref(0)
const duration = ref(0)
const audio = new Audio()
const countPause = ref(0)

const hasShownMessage = ref(false)
let playbackInterval = null
const playbackSeconds = ref(0)

const canvasRef = ref(null)
let audioContext, analyser, dataArray, source, animationId
let shuffledIndices = []

const currentSongName = computed(() => songs.value[currentIndex.value]?.name || '')

function setupVisualizer() {
    audioContext = new (window.AudioContext || window.webkitAudioContext)()
    analyser = audioContext.createAnalyser()
    analyser.fftSize = 256
    const bufferLength = analyser.frequencyBinCount
    dataArray = new Uint8Array(bufferLength)

    source = audioContext.createMediaElementSource(audio)
    source.connect(analyser)
    analyser.connect(audioContext.destination)

    drawVisualizer()

    if (audioContext.state === 'suspended') {
        const resume = () => {
            audioContext.resume()
            document.removeEventListener('touchstart', resume)
        }
        document.addEventListener('touchstart', resume, { once: true })
    }
}

function drawVisualizer() {
    const mainCanvas = canvasRef.value
    const mainCtx = mainCanvas.getContext('2d')
    const width = mainCanvas.width
    const height = mainCanvas.height
    const centerY = height / 2

    function draw() {
        animationId = requestAnimationFrame(draw)
        analyser.getByteFrequencyData(dataArray)

        // Canvas principal
        mainCtx.clearRect(0, 0, width, height)
        const barWidth = width / dataArray.length
        let x = 0
        for (let i = 0; i < dataArray.length; i++) {
            const value = dataArray[i]
            const barHeight = value / 2
            const gradient = mainCtx.createLinearGradient(0, centerY - barHeight, 0, centerY + barHeight)
            gradient.addColorStop(0, '#00f')
            gradient.addColorStop(1, '#0ff')
            mainCtx.fillStyle = gradient
            mainCtx.fillRect(x, centerY - barHeight, barWidth, barHeight)
            mainCtx.fillRect(x, centerY, barWidth, barHeight)
            x += barWidth + 1
        }

        // Mini visualizador (solo para la canción actual)
        const miniCanvas = miniCanvasRefs.value[currentIndex.value]
        if (miniCanvas) {
            const miniCtx = miniCanvas.getContext('2d')
            miniCtx.clearRect(0, 0, miniCanvas.width, miniCanvas.height)
            const miniBarWidth = miniCanvas.width / dataArray.length
            let mx = 0
            for (let i = 0; i < dataArray.length; i++) {
                const value = dataArray[i]
                const barHeight = value / 3
                miniCtx.fillStyle = '#0af'
                miniCtx.fillRect(mx, miniCanvas.height - barHeight, miniBarWidth, barHeight)
                mx += miniBarWidth + 1
            }
        }
    }

    draw()
}

function shuffleArray(length) {
    const indices = [...Array(length).keys()]
    for (let i = indices.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
            ;[indices[i], indices[j]] = [indices[j], indices[i]]
    }
    return indices
}
function SetMoment() {
    moment.value = false
}

function loadTrack(index) {
    const song = songs.value[index]
    if (!song) return
    audio.src = song.url
    audio.load()
    // playbackSeconds.value = 0
    stopPlaybackTimer()
    if (isPlaying.value == true) {
        audio.play().then(() => {
            isPlaying.value = true
            startPlaybackTimer()
        }).catch(err => {
            console.warn('iOS puede haber bloqueado la reproducción:', err)
        })
    }
    else {
        isPlaying.value = false
    }
}

function togglePlay() {
    if (audio.paused) {
        audio.play()
        isPlaying.value = true
        startPlaybackTimer()
    } else {
        audio.pause()
        isPlaying.value = false
        stopPlaybackTimer()
        countPause.value++;
        if (playbackSeconds.value >= 110 | (playbackSeconds.value >= 107 && countPause.value > 2)) {
            moment.value = true;
            playbackSeconds.value = 0;
            hasShownMessage.value = true
            countPause.value = 0
        }
    }
}

function startPlaybackTimer() {
    if (playbackInterval) return // Evita múltiples intervalos

    playbackInterval = setInterval(() => {
        playbackSeconds.value++
    }, 1000)
}

function stopPlaybackTimer() {
    clearInterval(playbackInterval)
    playbackInterval = null
}

function nextTrack() {
    if (isShuffle.value) {
        if (shuffledIndices.length === 0) {
            shuffledIndices = shuffleArray(songs.value.length)
        }
        currentIndex.value = shuffledIndices.shift()
    } else {
        currentIndex.value = (currentIndex.value + 1) % songs.value.length
    }
    isPlaying.value = true
    loadTrack(currentIndex.value)
}

function prevTrack() {
    if (isShuffle.value) {
        if (shuffledIndices.length === 0) {
            shuffledIndices = shuffleArray(songs.value.length)
        }
        currentIndex.value = shuffledIndices.shift()
    } else {
        currentIndex.value = (currentIndex.value - 1 + songs.value.length) % songs.value.length
    }
    isPlaying.value = true
    loadTrack(currentIndex.value)
}

function toggleShuffle() {
    isShuffle.value = !isShuffle.value

    if (isShuffle.value) {
        shuffledIndices = shuffleArray(songs.value.length)
        // Evita repetir la canción actual
        shuffledIndices = shuffledIndices.filter(i => i !== currentIndex.value)
    } else {
        shuffledIndices = []
    }
}

function seekAudio() {
    audio.currentTime = currentTime.value
}

audio.addEventListener('timeupdate', () => {
    currentTime.value = audio.currentTime
    duration.value = audio.duration || 0
})

audio.addEventListener('ended', () => {
    nextTrack()
})

function formatTime(time) {
    const minutes = Math.floor(time / 60)
    const seconds = Math.floor(time % 60).toString().padStart(2, '0')
    return `${minutes}:${seconds}`
}

onMounted(() => {
    setupVisualizer()
    loadTrack(currentIndex.value)
})

onBeforeUnmount(() => {
    cancelAnimationFrame(animationId)
    audio.pause()
    audio.src = ''
    if (audioContext) audioContext.close()
})



const editingIndex = ref(null)
const editedName = ref('')

function playSong(index) {
    isPlaying.value = true
    currentIndex.value = index
    loadTrack(index)
    startPlaybackTimer()
    isPlaying.value = true
}

function removeSong(index) {
    if (currentIndex.value === index) {
        audio.pause()
    }
    songs.value.splice(index, 1)
    if (currentIndex.value >= songs.value.length) {
        currentIndex.value = 0
    }
    loadTrack(currentIndex.value)
}

function editSong(index) {
    editingIndex.value = index
    editedName.value = songs.value[index].name
}

function saveEdit(index) {
    if (editedName.value.trim()) {
        songs.value[index].name = editedName.value.trim()
    }
    editingIndex.value = null
}

const miniCanvasRefs = ref({})
// const canvasRefs = ref([])
function setMiniCanvasRef(el, index) {
    if (el) miniCanvasRefs.value[index] = el
    //***** */
    //else delete miniCanvasRefs.value[index]
}



const listRef = ref(null)

// Drag & touch
const isDragging = ref(false)
const readyIndex = ref(null)
const dragText = ref("")
const fingerOffsetY = ref(0)
const floatY = ref(0)
const containerRect = ref({ top: 0, bottom: 0, left: 0, width: 0 })

let touchTimer = null // temporizador para delay de drag

const floatingStyle = computed(() => ({
    position: "fixed",
    top: `${Math.min(Math.max(floatY.value - fingerOffsetY.value, containerRect.value.top),
        containerRect.value.bottom - 1)}px`,
    left: `${containerRect.value.left}px`,
    width: `${containerRect.value.width}px`,
    zIndex: 9999,
    pointerEvents: "none"
}))

function startTouch(index, e) {
    // Guardamos info para el posible drag
    const t = e.touches[0]
    const el = e.currentTarget
    const itemRect = el.getBoundingClientRect()
    const listBox = listRef.value.getBoundingClientRect()
    containerRect.value = {
        top: listBox.top,
        bottom: listBox.bottom,
        left: listBox.left,
        width: listBox.width
    }

    fingerOffsetY.value = t.clientY - itemRect.top
    floatY.value = t.clientY

    // Iniciamos temporizador de 2s para drag
    touchTimer = setTimeout(() => {
        isDragging.value = true
        currentIndex.value = index
        dragText.value = songs.value[index].name
    }, 500)
}

function onDrag(e) {
    if (!isDragging.value)
        return

    const t = e.touches[0]
    floatY.value = t.clientY

    const targetIndex = indexFromY(t.clientY)
    if (targetIndex == null || targetIndex === currentIndex.value) return

    const arr = songs.value
    const moved = arr.splice(currentIndex.value, 1)[0]
    arr.splice(targetIndex, 0, moved)
    currentIndex.value = targetIndex
}
// const canvasRefs = ref([])

function endDrag() {
    clearTimeout(touchTimer)
    if (!isDragging.value) return

    // Guardamos el índice del item que se soltó
    const releasedIndex = currentIndex.value

    // Reset de estado drag
    isDragging.value = false
    currentIndex.value = null
    dragText.value = ""
    readyIndex.value = null
    document.querySelectorAll('.lisongs').forEach(item => item.classList.remove('is-ready'))

    isPlaying.value = true
    currentIndex.value = releasedIndex
    // loadTrack(index)
    startPlaybackTimer()
    isPlaying.value = true
}


// function endDrag() {
//   clearTimeout(touchTimer)
//   if (!isDragging.value) return

//   const releasedIndex = currentIndex.value

//   isDragging.value = false
//   currentIndex.value = null
//   dragText.value = ""

//     readyIndex.value = null
//   document.querySelectorAll('.lisongs').forEach(item => item.classList.remove('is-ready'))

//   // Volvemos a dibujar el canvas del elemento soltado
//   if (canvasRefs.value[releasedIndex]) {
//     drawVisualizer(canvasRefs.value[releasedIndex], songs.value[releasedIndex])
//   }
// }

function indexFromY(y) {
    const container = listRef.value
    if (!container) return null
    const children = Array.from(container.querySelectorAll(".lisongs"))
    if (!children.length) return null
    for (let i = 0; i < children.length; i++) {
        const r = children[i].getBoundingClientRect()
        const mid = r.top + r.height / 2
        if (y < mid) return i
    }
    return children.length - 1
}

</script>

<style scoped>
.audio-player {
    text-align: center;
    background: #fff;
    padding: 1rem;
    border-radius: 8px;
    color: #555;
}

.visualizer {
    display: block;
    margin: 1rem auto;
    background-color: white;
}

.song-info {
    margin: 1rem 0;
}

.song-info h3 {
    margin: 0;
}

.song-info .time {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.song-info input[type=range] {
    width: 100%;
}

.controls button {
    background: none;
    border: none;
    margin: 0 1rem;
    cursor: pointer;
    color: white;
}

.controls img {
    width: 40px;
    height: 40px;
}

.controls .active {
    color: lime;
}

.shuffle-button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #aaa;
}

.shuffle-button.active {
    color: #0ff;
}

.mini-visualizer {
    margin-top: 4px;
    background-color: #fff;
    display: block;
}

.song-item.active-song {
    background-color: #e8f8ff;
    border-left: 4px solid #0af;
}

.lisongs {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
    /* cursor: move; */
    border: 1px solid #D6D6D6;
    /* border-radius: 10px; */
}

.btn-audio {
    border: none;
    background-color: transparent;
    border-left: 1px solid #D6D6D6;
    padding: 7px 10px;
}

.span-audio {
    background-color: #F4F4F4;
    padding-top: 0px;
    padding-bottom: 0px;
    border-left: 1px solid #D6D6D6;
}

.span-audio>input {
    border: none;
    width: 100%;
    padding-left: 10px;
}

.span-audio>input:focus {
    border: none;
    width: 100%;
    padding-left: 10px;
    outline: none;
}

.span-audio>label {
    padding-top: 7px;
    padding-bottom: 7px;
    padding-left: 10px;
}

.svg {
    transform: rotate(180deg);
}

.lista {
    /* width: 320px;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    gap: 8px; */
    touch-action: none;
    /* clave en iOS para permitir touchmove continuo */
    -webkit-user-select: none;
    user-select: none;
}

.item {
    /* padding: 14px 16px;
    background: #f9f9f9;
    border: 2px solid #ccc;
    border-radius: 10px;
    text-align: center;
    font-size: 18px; */
}

/* Oculta el original pero mantiene el espacio para que no colapse el layout */
.is-source {
    visibility: hidden;
}

/* Ítem flotante que sigue el dedo */
.floating {
    background: #e3f2fd;
    border: 2px solid #90caf9;
    border-radius: 12px;
    box-shadow: 0 10px 24px rgba(0, 0, 0, 0.18);
    padding: 14px 16px;
}

/* Animación de desplazamiento al reordenar */
.list-move {
    transition: transform 0.2s ease;
}

.floating {
    background: #e3f2fd;
    border: 2px solid #90caf9;
    border-radius: 12px;
    padding: 8px 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.18);
    pointer-events: none;
}

/* Ítem seleccionado para drag (después del delay) */
.is-ready {
    transform: scale(1.05);
    /* aumenta tamaño ligeramente */
    background-color: #d1eaff;
    /* ilumina un poco */
    border-color: #42a5f5;
    /* opcional: borde más fuerte */
    transition: transform 0.2s, background-color 0.2s, border-color 0.2s;
}
</style>